<template>
  <div>
    <p style="font-size: 16px;">我的消息</p>
    <div class="table_msg">
      <table style="width: 100%;border-collapse: collapse;margin-bottom: 40px;">
        <tr v-for="item in notice">
          <td style="border: 1px solid #bbb;">
            <div class="msg_tt">
              <i @click="showmsginfo($event)" class="iconfontyyy"
                 style="cursor: pointer;position:absolute;left: 10px;top: 7px;">&#xe611;</i>
              <p><span class="msg_title" v-html="item.title"></span><span
                style="float: right" v-html="item.up_time"></span></p>
              <div style="margin-top: 20px;" v-html="item.content"></div>
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        notice: []
      }
    },
    created(){
      var that = this;
      $.ajax({
        url: "http://houxiaopang.com/api/v1.1/designerdash/notice/all",
        type: "GET",
        headers: {"Authorization": "Token " + token},
        success(data) {
          that.notice = [];
          if (data.code === 0) {
            for (var i = 0, size = data.data.notice.length; i < size; i++) {
              that.notice.push(data.data.notice[i]);
            }
          } else {
            alert("网络拥挤，请稍后再试···");
          }
        },
        error(){
          alert("网络拥挤，请稍后再试···");
        }
      });
    },
    methods: {
      showmsginfo(e){
        //&#xe820; xe611
        var i = e.target;
        var msgmore = i.nextElementSibling.nextElementSibling;
        if (msgmore.style.display === "none") {
          i.innerHTML = "&#xe820;";
          msgmore.style.display = "block";
        } else {
          i.innerHTML = "&#xe611;";
          msgmore.style.display = "none";
        }
      }
    }
  }
</script>
<style>
  .table_msg {
    width: 800px;
    margin: 40px auto;
  }

  .table_msg .msg_tt {
    padding: 10px 40px;
    min-height: 40px;
    position: relative;
  }

  .table_msg .msg_tt > p {
    line-height: 20px;
    overflow: hidden;
  }

  .table_msg .msg_tt .msg_title {
    width: 80%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    float: left;
    color: #313131;
  }

  .table_msg .msg_tt .iconfontyyy:hover {
    color: #d01667;
  }
</style>



